const throttle = (callback, ms) => {
  let currentArgs = [];
  let lastCall = -Infinity;
  let timer = null;

  const throttled = (...args) => {
    const now = Date.now(); // —Å–æ–∑–¥–∞–µ–º –≤—Ä–µ–º–µ–Ω–Ω—É—é –º–µ—Ç–∫—É
    currentArgs = args; // —Å–æ—Ö—Ä–∞–Ω—è–µ–º –ø–æ—Å–ª–µ–¥–Ω–∏–µ –∞—Ä–≥—É–º–µ–Ω—Ç—ã –∫–∞–∫ –∞–∫—Ç—É–∞–ª—å–Ω—ã–µ
    clearTimeout(timer); // —á–∏—Å—Ç–∏–º —Å—Ç–∞—Ä—ã–π —Ç–∞–π–º–∞—É—Ç

    // –µ—Å–ª–∏ —Å –ø–æ—Å–ª–µ–¥–Ω–µ–≥–æ –≤—ã–∑–æ–≤–∞ –ø—Ä–æ—à–ª–æ –Ω–µ –º–µ–Ω—å—à–µ, —á–µ–º ms
    if (now - lastCall >= ms) {
      // –≤—ã–∑—ã–≤–∞–µ–º –æ—Ä–∏–≥–∏–Ω–∞–ª—å–Ω—É—é —Ñ—É–Ω–∫—Ü–∏—é
      callback(...currentArgs);
      // –ø–µ—Ä–µ–∑–∞–ø–∏—Å—ã–≤–∞–µ–º –≤—Ä–µ–º—è –ø–æ—Å–ª–µ–¥–Ω–µ–≥–æ –≤—ã–∑–æ–≤–∞
      lastCall = now;
    } else {
      // –∑–¥–µ—Å—å –ø—Ä–æ—à–ª–æ –≤—Ä–µ–º–µ–Ω–∏ –º–µ–Ω—å—à–µ, —á–µ–º ms
      const timeout = lastCall + ms - now;
      // –ø–æ—ç—Ç–æ–º—É –ø–ª–∞–Ω—Ä—É–µ–º –Ω–æ–≤—ã–π –≤—ã–∑–æ–≤ —á–µ—Ä–µ–∑ timeout
      timer = setTimeout(() => throttled(...currentArgs), timeout);
    }
  };

  return throttled;
};

export default throttle;
